<template>
	<header class="header">
		<div class="header-item logo">
			<img src="../assets/images/logo.png" alt="">
		</div>
		<div class="nav-list" @click="onMenuClicked" v-show="menuVisible">
			<div class="header-item m-nav">
				<div class="m-nav-inner">{{$t("header.0")}}</div>
			</div>
			<div class="header-item m-nav">
				<div class="m-nav-inner">{{$t("header.1")}}</div>
			</div>
			<div class="header-item m-nav">
				<div class="m-nav-inner">{{$t("header.2")}}</div>
			</div>
			<div class="header-item m-nav">
				<div class="m-nav-inner">{{$t("header.3")}}</div>
			</div>
			<div class="header-item m-nav">
				<div class="m-nav-inner">{{$t("header.4")}}</div>
			</div>
			<div class="header-item m-nav">
				<div class="m-nav-inner">{{$t("header.5")}}</div>
			</div>
			<div class="header-item m-nav">
				<div class="m-nav-inner" @click="switchLang">{{$t("header.6")}}</div>
			</div>
		</div>
		<div class="header-item m-buy-btn">
			<div class="m-buy-btn-inner" onclick="window.open('https://exchange.pancakeswap.finance/#/swap')">{{$t("header.btn")}}</div>
		</div>
		<div class="menu-icon" @click="menuVisible = !menuVisible"></div>
	</header>
</template>
<script>
    export default {
        name: "m-header",
        data() {
            return {
                menuVisible: document.documentElement.clientWidth > 375,
            }
        },
        methods: {
            switchLang() {
                this.$i18n.locale = this.$i18n.locale == "en" ? "cn" : "en";
                localStorage.setItem("app:locale",this.$i18n.locale);
            },
		        onMenuClicked(){
                if(document.documentElement.clientWidth <= 375) {
                    this.menuVisible = false;
                }
		        }
        }
    }
</script>
<style lang="scss">
	$header-h: 97px;
	$header-font-size: 20px;
	.header {
		width: 100vw;
		height: $header-h;
		position: absolute;
		display: flex;
		align-items: center;
		justify-content: space-around;
		user-select: none;
		z-index: 1000;
	}

	.nav-list {
		width: calc(100vw - 400px);
		display: flex;
		align-items: center;
		justify-content: space-around;
	}

	.header-item {
		flex: 1;
		height: $header-h;


		&.m-nav {
			flex: auto;
			height: $header-h;
			text-align: center;

			.m-nav-inner {
				position: relative;
				min-width: 100px;
				font-size: $header-font-size;
				font-weight: 400;
				color: #FFFFFF;
				line-height: $header-h;
				cursor: pointer;
				transition: all ease .3s;

				&:after {
					content: "";
					display: block;
					position: relative;
					width: 80px;
					height: 4px;
					margin: -20px auto 0 auto;
					background-color: #fff;
					border-radius: 2px;
					opacity: 0;
					transform: translateY(5);
					transition: all ease .3s;
				}

				&:hover {
					&:after {
						opacity: 1;
						transform: translateY(0px);
					}
				}
			}
		}


		&.logo {
			flex: 80px;
			text-align: center;
			height: $header-h;
			cursor: pointer;

			img {
				display: block;
				margin: 20px auto 0 auto;
				width: 54px;
			}

			&:hover {
				img {
					animation: ring linear 0.8s infinite;
				}
			}
		}

		@keyframes ring {
			0% {
				transform: rotate(0deg);
			}
			10% {
				transform: rotate(-15deg);
			}
			20% {
				transform: rotate(15deg);
			}
			30% {
				transform: rotate(-15deg);
			}
			40% {
				transform: rotate(15deg);
			}
			50% {
				transform: rotate(0deg);
			}
		}

		&.m-buy-btn {
			flex: 180px;
			text-align: center;
			height: $header-h;

			.m-buy-btn-inner {
				margin: 16px auto auto auto;
				width: 158px;
				height: 54px;
				border-radius: 41px;
				border: 4px solid #FCA311;
				color: #FFFFFF;
				font-size: $header-font-size;
				line-height: 54px;
				cursor: pointer;
				background-color: rgba(#fff, 0);
				transition: all ease .3s;

				&:hover {
					background-color: rgba(#fff, 0.1);
				}
			}
		}
	}

	.menu-icon {
		display: none;
	}

	@media (max-width: 376px) {
		.header {
			position: absolute;
			width: 100vw;
			display: block;
			align-items: flex-start;
		}

		.logo.logo.logo {
			position: absolute;
			left: 20px;
			height: 50px;

			img {
				width: 40px;
				margin: 20px auto 0 auto;
				/*height: 30px;*/
			}
		}
		.m-buy-btn.m-buy-btn {
			position: absolute;
			right: 20px;

			.m-buy-btn-inner {
				width: 108px;
				height: 34px;
				line-height: 34px;
				font-size: 14px;
			}
		}
		.nav-list {
			position: fixed;
			background-color: rgba(#000, 0.9);
			display: block;
			top: 70px;
			bottom: 0;
			width: 100vw;

		}
		.menu-icon {
			position: absolute;
			display: block;
			background-image: url("../assets/icon/menu.png");
			background-size: contain;
			width: 40px;
			height: 40px;
			right: 160px;
			top: 15px;
		}
	}
</style>
