<template>
	<div class="faq">
		<div class="t1">{{$t("faq.t1")}}</div>
		<div class="t2">{{$t("faq.t2")}}</div>
		<div class="m-line"></div>
		<div class="q-list">
			<div class="list-item" v-for="(q,i) in list" @click="toggle(i)">
				<div class="q">
					<div class="q-inner">{{q.q}}</div>
					<div class="right-icon" :class="{'active':active == i}"></div>
				</div>
				<div class="a" v-if="active == i">{{q.a}}</div>
			</div>
		</div>
	</div>
</template>
<script>
    export default {
        name: "m-faq",
        data() {
            const list = [];
            for (var i = 0; i < 9; i++) {
                list.push({
                    q: this.$t(`faq.q[${i}].q`),
                    a: this.$t(`faq.q[${i}].a`),
                })
            }
            return {
                list,
                active: 0,
            };
        },
        methods: {
            toggle(i) {
                if (this.active == i) {
                    this.active = -1;
                } else {
                    this.active = i;
                }
            }
        }
    }
</script>
<style lang="scss" scoped>
	.faq {
		position: relative;
		overflow: hidden;
	}
	
	.t1 {
		height: 46px;
		font-size: 33px;
		font-weight: 400;
		color: #FFFFFF;
		line-height: 46px;
	}
	
	.t2 {
		margin-top: 10px;
		height: 74px;
		font-size: 53px;
		font-weight: 400;
		color: #FFFFFF;
		line-height: 74px;
	}
	
	.m-line {
		margin: 35px auto 0 auto;
		width: 126px;
		height: 8px;
		background: #FCA311;
		border-radius: 3px;
	}
	
	.q-list {
		margin-top: 91px;
		display: flex;
		flex-wrap: wrap;
		flex-direction: row;
		justify-content: space-between;
	}
	
	.list-item {
		width: 570px;
		margin-bottom: 15px;
		
		
		.q {
			position: relative;
			height: 98px;
			background: rgba(#1F2670, 0.5);
			border-radius: 16px;
			width: 100%;
			box-sizing: border-box;
			padding: 0 38px;
			text-align: left;
			
			&:before {
				content: "";
				display: inline-block;
				vertical-align: middle;
				height: 98px;
				width: 1px;
			}
			
			.q-inner {
				display: inline-block;
				vertical-align: middle;
				width: 440px;
				font-size: 18px;
				color: #FFFFFF;
				line-height: 24px;
			}
			
			.right-icon {
				position: absolute;
				top: 38px;
				right: 50px;
				width: 20px;
				height: 20px;
				background-image: url("../assets/icon/right-arrow.png");
				background-size: contain;
				transform: rotate(0);
				transition: ease .3s;
				
				&.active {
					transform: rotate(90deg);
				}
			}
		}
		
		.a {
			width: 552px;
			font-size: 16px;
			color: #FFFFFF;
			line-height: 24px;
			text-align: left;
			box-sizing: border-box;
			padding: 30px 10px;
			margin-bottom: 30px;
			opacity: 0;
			transform: scaleY(0);
			animation: ashow 0.4s ease forwards;
			transform-origin: top center;
		}
	}
	
	@keyframes ashow {
		from {
			opacity: 0;
			transform: scaleY(0);
		}
		to {
			opacity: 1;
			transform: scaleY(1);
		}
	}
	
	@media (max-width: 376px) {
		.faq.faq {
			width: 285px;
			margin: 150px auto 0 auto;
			
			.t1 {
				height: 23px;
				font-size: 16px;
				line-height: 23px;
			}
			
			.t2 {
				margin-top: 5px;
				height: 37px;
				font-size: 26px;
				line-height: 37px;
			}
			
			.m-line {
				margin: 17px auto 0 auto;
				width: 63px;
				height: 4px;
			}
			
			.q-list {
				margin-top: 45px;
				display: block;
			}
			
			.list-item {
				width: 100%;
				margin-bottom: 8px;
				
				.q {
					height: 49px;
					border-radius: 8px;
					width: 100%;
					padding: 0 19px;
					
					&:before {
						height: 49px;
					}
					
					.q-inner {
						display: inline-block;
						vertical-align: middle;
						width: 220px;
						font-size: 9px;
						color: #FFFFFF;
						line-height: 12px;
					}
					
					.right-icon {
						top: 19px;
						right: 25px;
						width: 10px;
						height: 10px;
					}
				}
				
				.a {
					width: 100%;
					font-size: 8px;
					line-height: 14px;
					padding: 15px 5px;
					margin-bottom: 15px;
					opacity: 0;
				}
			}
		}
		
		
	}
</style>
